import axios from 'axios' //http
import qs from 'qs'
import router from '../router'

//axios
axios.defaults.baseURL = 'http://shop.ueoa.net'
//
axios.defaults.timeout = 5000
// post

axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'
axios.defaults.withcredentials = true

//()
axios.interceptors.request.use((config) => {
  const token = localStorage.getItem("token")
  //
  if (token) {
    config.headers['Authorization'] = token
    config.headers['token'] = token
  }
  return config;
}, (error) => {
  return Promise.reject(error);
});

// 
axios.interceptors.response.use(
  (res) => {
    const ret = res.data;

        if (ret.status !== 200 && !ret.key && !ret.code) {
          if (ret.code == 101) {
             //
              localStorage.setItem('token','');
              Location.href="/login";
             reject(ret.msg)
        }
      }
    return res
  },
  (error) => {
    /*Message({
      showClose: true,
      message: error.message,
      type: 'error',
      duration: 2000
    })*/
    return Promise.reject(error);
  }
);

// get
function get(url, data) {
  return new Promise((resolve, reject) => {
    axios.get(url, qs.stringify(data)).then(res => {
      resolve(res.data);
    }).catch(err => {
      reject(err.data);
    })
  });
}

// post
function post(url, params) {
  return new Promise((resolve, reject) => {
    axios.post(url, qs.stringify(params)).then(res => {
      resolve(res.data);
    }).catch(err => {
      reject(err.data);
    })
  });
}

// post 
function del(url, params) {
  return new Promise((resolve, reject) => {
    axios.delete(url, params).then(res => {
      resolve(res.data);
    }).catch(err => {
      reject(err.data);
    })
  });
}

// post
function put(url, params) {
  //Promise es6  resolve//  reject// async  ...
  return new Promise((resolve, reject) => {
     //axios  method (url,data)
     //axios.put(url, params) //   default host   
    axios.put(url, params).then(res => {
      resolve(res.data);
    }).catch(err => {
      reject(err.data);
    })
  });
}

// url  methoddata
//request({'http://www.baidu.com',get,[{wd:'123456'}]});
export function request({url,method,data}) {
  data['token']='123456';
  if (method == 'get') {

    return get(url, data);
  } else if (method == 'post') {
    return post(url, data);
  }else if (method == 'delete') {
    return del(url, data);
  }else if (method == 'put') {
    return put(url, data);
  }
}
//
export default request



